.calendertitle {
  margin-bottom: 10px;
}

.calendercontrol {
  display: grid;
  grid-template-columns: 130px 90px;
  grid-column-gap: 10px;
  padding: 10px 0px;
}

.calendercontext {
  height: 130px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  position: relative;
}

.calendercontextMonthRows {
  height: 130px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  position: relative;
}

.calenderdisplayinformation {
  border: #f5a623 1px solid;
  height: 400px;
  width: calc(100% - 10px);
  background-color: white;
  position: absolute;
  top: 140px;
  left: 5px;
  z-index: 100;
}

.calenderdisplayinformationnone{
  display: none;
}

.calenderModule{
  display: flex;
  height: 50px;
  justify-content: space-between;
  align-items: center;
  padding: 0px 5px;
  .Modulelayout{
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-column-gap: 10px;
  }
}
