.Wardchartlayout{
    display: grid;
    grid-template-columns: 1fr 1fr;
    .halfwardchart{
        display: grid;
        grid-template-rows: auto auto auto;
        .singlewardchart{
            display: grid;
            grid-template-columns: 80px 1fr;
            .firstRowTitle{
                display: flex;
                padding-top: 55px;
                justify-content: center;
                align-items: center;
            }
            .secondRowTitle{
                display: flex;
                align-items: center;
                justify-content: center;
                align-items: center;
            }
            .thirdRowTitle{
                display: flex;
                justify-content: center;
                padding-top: 15px;
                align-items: center;
            }
        }
    }
}