.noticetitle{
    height: 50px;
    padding: 0px 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #cccccc;
}

.noticecontextborder{
    min-height: 50px;
    max-height: 250px;
    overflow-y: auto;
    .noticecontext{
        height: 50px;
        padding: 0px 5px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &:hover{
            background-color: #DFE5E9;
        }
    }
}

.noticeedit{
    background-color: rgba(238,238,238,1);
    display: flex;
    justify-content: center;
    align-items: center;
}