.RTtablecell{
    display: grid; 
    grid-template-columns: 1fr 6fr;
    position: relative;
    &:hover{
        background-color: #DFE5E9;
    }
    .RTBreathDateCell{
        display: flex;
        align-items: center;
        justify-content: center;
        border: rgba(238,238,238,1) 1px solid;
        border-collapse: collapse;
    }
}