.prescriptiontablecell {
  position: relative;
  &:hover {
    background-color: #dfe5e9;
  }
}

.prescriptiontablecellcontext {
  display: grid; //flex
  align-items: center;
  border: rgba(238, 238, 238, 1) 1px solid;
  border-collapse: collapse;
  // align-items: center;
  justify-content: center;
  // padding: 0px 5px;
  .prescriptiontablecellcontexttext {
    word-wrap: break-word;
    overflow:hidden;
    text-overflow: ellipsis;
  }
}
