.linkstate{
  background-Color: rgba(59, 151, 225, 1);
  display: flex;
  padding-Left: 15px;
  padding-Right: 15px;
  height: 40px;
}

.dislinkstate{
  background-Color: rgba(0, 0, 0, 0);
  display: flex;
  padding-Left: 15px;
  padding-Right: 15px;
  height: 40px;
}

.linkfont {
  color: rgba(25, 70,90, 1);
  font-weight: 600px;
  font-size: 18px;
  margin: 0px 10px 0px 2px;
  &:hover {
    color:red;
  }
}
.dislinkfont{
  color: rgba(46, 100, 164, 1);
  font-weight: 600px;
  font-size: 18px;
  margin: 0px 10px 0px 2px;
  &:hover {
    color:red;
  }
}
