.scheduletodotitle{
    height: 50px;
    padding: 0px 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #cccccc;
}

.scheduletodocell{
    border: rgba(238,238,238,1) 1px solid;
    border-collapse: collapse;
    height: 50px;
    display: flex;
    padding: 0px 5px;
    align-items: center;
    &:hover{
        background-color: #DFE5E9;
    }
}

.finishbutton{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 16px;
    border: rgba(245, 166, 35, 1) 1px solid;
    color: rgba(245, 166, 35, 1);
}

.todobutton{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 16px;
    border: rgba(245, 166, 35, 1) 1px solid;
    background-color: rgba(245, 166, 35, 1);
    color: white;
}