.ScheduleEventLayout {
  border: rgba(238, 238, 238, 1) 1px solid;
  border-radius: 4px;
  margin-top: 10px;
  .ScheduleEventTitle {
    height: 35px;
    background-color: #71b0e46e;
    padding: 0px 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }
  .ScheduleEventContext {
    padding: 0px 16px;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    .ContextControl {
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      .buttonGroup {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 5px;
        .buttonselect {
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 16px;
          padding: 0px 20px;
          background-color: rgba(245, 166, 35, 1);
          cursor: pointer;
          .titlecolor {
            color: white;
          }
        }
        .buttonUnselect {
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 16px;
          padding: 0px 16px;
          border: rgba(245, 166, 35, 1) 1px solid;
          cursor: pointer;
          .titlecolor {
            color: rgba(245, 166, 35, 1);
          }
        }
      }
    }
    .ContextList {
      max-height: 150px;
      min-height: 50px;
      overflow-y: auto;
      .CellLayout {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0px 5px;
        height: 50px;
        .Context {
          display: grid;
          grid-template-columns: repeat(4, auto);
          grid-column-gap: 10px;
        }
      }
    }
  }
}
