.calendersubcontextclick {
  display: grid;
  grid-template-rows: 50px 1fr 1fr;
  border: 1px #f5a623 solid;
  border-collapse: collapse;
  position: relative;
}



.calendersubcontexticonsecond {
  display: grid;
  grid-template-columns: auto auto;
  grid-column-gap: 30px;
  .displayinfo{
    color: rgba(238, 238, 238, 1);
  }
}

.calendersubcontexticon {
  display: flex;
  justify-content: center;
  align-items: center;
}

.calendersubcontexttitle {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(238, 238, 238, 1);
}
.calendersubcontexttitleclick {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f5a623;
}

.calendersubcontexttitletoday {
  font-family: "PingFangSC-Semibold";
}

.calendersubcontext {
  display: grid;
  grid-template-rows: 50px 1fr 1fr;
  border: 1px rgba(238, 238, 238, 1) solid;
  border-collapse: collapse;
}
