.treatmentordercell {
  position: relative;
  &:hover {
    background-color: #dfe5e9;
  }
  .cellstyle {
    display: flex;
    align-items: center;
    padding: 0px 5px;
    border: rgba(238, 238, 238, 1) 1px solid;
    border-collapse: collapse;
    word-wrap: break-word;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
