.nurtionfeedingcell{
    &:hover{
        background-color: #DFE5E9;
    }
    .cellforstate{
        display: grid;
        grid-template-columns:  1fr 1fr 2fr;
        .celllayout{
            border: rgba(232,232,232,1) 1px solid;
            border-collapse: collapse;
            padding: 0px 5px;
            display: flex;
            align-items: center;
            height: 50px;
        }
    }
    .cellforremark{
        border: rgba(232,232,232,1) 1px solid;
        border-collapse: collapse;
        padding: 0px 5px;
        display: flex;
        align-items: center;
        height: 50px;
    }
}